<template>
  <div>
    <div class="nav">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>新建用户信息</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-row>
        <el-col :span="6">
          <el-form-item label="用户姓名" prop="username">
            <el-input
              v-model="ruleForm.username"
              :label-width="formLabelWidth"
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="联系方式" prop="call">
            <el-input
              type="call"
              maxlength="11"
              v-model.number="ruleForm.call"
              :label-width="formLabelWidth"
              auto-complete="off"
            ></el-input>
          </el-form-item>
        </el-col>

        <el-col :span="6">
          <el-form-item label="身份证号" prop="sfz">
            <el-input
              maxlength="18"
              v-model="ruleForm.sfz"
              label-width="140px"
              @blur="sex"
            ></el-input
          ></el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="性别" prop="sex">
            <el-input
              v-model="ruleForm.sex"
              label-width="140px"
              :disabled="true"
            ></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-form-item label="详细地址" prop="address">
        <el-input v-model="ruleForm.address"></el-input>
      </el-form-item>

      <el-form-item label="是否购车" prop="buy">
        <el-switch v-model="ruleForm.buy" @change="changeStatus"></el-switch>
      </el-form-item>

      <!-- 默认false if yes to true -->
      <div :style="{ display: show }">
        <el-form-item label="购车时间">
          <el-col :span="8">
            <el-date-picker
              type="date"
              placeholder="购车时间"
              v-model="ruleForm.date1"
              style="width: 100%"
            ></el-date-picker>
          </el-col>
        </el-form-item>
        <el-form-item label="车辆品牌" prop="Moto">
          <el-checkbox-group v-model="ruleForm.Moto">
            <el-checkbox label="豪爵" name="haojue"></el-checkbox>
            <el-checkbox label="铃木" name="suzuk"></el-checkbox>
            <el-checkbox label="新大洲本田" name="xdz"></el-checkbox>
            <el-checkbox label="五羊本田" name="wy"></el-checkbox>
            <el-checkbox label="上海建设" name="js"></el-checkbox>
            <el-checkbox label="珠峰" name="zf"></el-checkbox>
            <el-checkbox label="其他" name="other" @change="otherMoto"
              >其他
              <el-input
                v-model="ruleForm.OtherMoto"
                :style="{ display: this.ShowMoto ? 'none' : '' }"
              ></el-input>
            </el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="车辆型号" prop="MotoType">
          <el-col :span="8">
            <el-input v-model="ruleForm.MotoType" autocomplete="off"></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="购车价格" prop="Money">
          <el-col :span="8">
            <el-input v-model="ruleForm.Money" autocomplete="off"></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="车牌号" prop="brand">
          <el-col :span="8">
            <el-input v-model="ruleForm.brand" autocomplete="off"></el-input>
          </el-col>
        </el-form-item>
      </div>
      <!-- 买保险 -->
      <el-col :span="24">
        <el-col :span="8">
          <el-form-item label="购保时间：">
            <el-date-picker
              type="date"
              placeholder="购保时间"
              v-model="ruleForm.date"
              style="width: 90%"
            ></el-date-picker>
          </el-form-item>
        </el-col>
        <!-- 审车 -->
        <el-col :span="8">
          <el-form-item label="首次审车：" >
            <el-date-picker
              type="date"
              placeholder="首次审车时间"
              v-model="ruleForm.date_sc"
              style="width: 90%"
            ></el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="初领驾驶证：">
            <el-date-picker
              type="date"
              placeholder="初次审领驾驶证时间"
              v-model="ruleForm.date_jsz"
              style="width: 90%"
            ></el-date-picker>
          </el-form-item>
        </el-col>
      </el-col>
      <!-- 买保险 -->
      <el-col :span="7">
        <el-form-item label="行驶证图片：" prop="img">
          <el-col :span="12">
            <el-upload
              drag
              action="#"
              ref="upload"
              v-model="ruleForm.img"
              list-type="picture-card"
              :on-preview="img"
              :on-remove="handleRemove"
              :on-change="imgChange"
              :auto-upload="false"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog :visible.sync="dialogVisible" size="tiny">
              <img width="100%" :src="dialogImageUrl" alt />
            </el-dialog>
          </el-col>
          <el-col :span="12">
            <el-upload
              drag
              action="#"
              ref="upload_img_xszfm"
              v-model="ruleForm.img_xszfm"
              list-type="picture-card"
              :on-preview="img_xszfm"
              :on-remove="handleRemove"
              :on-change="img_xszfm_change"
              :auto-upload="false"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog :visible.sync="dialogVisible" size="tiny">
              <img width="100%" :src="img_xszfm" alt />
            </el-dialog>
          </el-col>
          <!-- 反面 -->
        </el-form-item>
      </el-col>
      <!-- 合格证图片 -->
      <el-col :span="5">
        <el-form-item label="车辆合格证：" prop="img_qs">
          <el-upload
            drag
            action="#"
            ref="upload_qs"
            v-model="ruleForm.img_qs"
            list-type="picture-card"
            :on-preview="img_qs"
            :on-remove="handleRemove"
            :on-change="img_qs_change"
            :auto-upload="false"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible" size="tiny">
            <img width="100%" :src="img_qs" alt />
          </el-dialog>
        </el-form-item>
      </el-col>
      <!-- 合格证 -->
      <!-- 新车合照 -->
      <el-col :span="5">
        <el-form-item label="新车合照：" prop="img_new">
          <el-upload
            drag
            action="#"
            ref="upload_new"
            v-model="ruleForm.img_new"
            list-type="picture-card"
            :on-preview="img_new"
            :on-remove="handleRemove"
            :on-change="img_new_change"
            :auto-upload="false"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible" size="tiny">
            <img width="100%" :src="img_new" alt />
          </el-dialog>
        </el-form-item>
      </el-col>
      <el-col :span="6">
        <el-form-item label="驾驶证：" prop="img_jsz">
          <el-upload
            drag
            action="#"
            ref="upload_jsz"
            v-model="ruleForm.img_jsz"
            list-type="picture-card"
            :on-preview="img_jsz"
            :on-remove="handleRemove"
            :on-change="img_jsz_change"
            :auto-upload="false"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible" size="tiny">
            <img width="100%" :src="img_new" alt />
          </el-dialog>
        </el-form-item>
      </el-col>
      <!-- 新车合照 -->
      <el-col :span="24">
        <el-form-item label="备注" prop="desc">
          <el-input type="textarea" v-model="ruleForm.desc"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="24">
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"
            >添加</el-button
          >
          <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
      </el-col>
    </el-form>
  </div>
</template>
<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      // 添加成功
      options: [
        {
          value: "man",
          label: "男",
        },
        {
          value: "wuman",
          label: "女",
        },
        {
          value: "other",
          label: "保密",
        },
      ],
      addcomputed: false,
      dialogFormVisible: false,
      dialogTableVisible: false,
      dialogVisible: false,
      dialogImageUrl: "",
      show: "none",
      ShowMoto: true,
      Motochecked: true,
      ruleForm: {
        username: "",
        call: "",
        sfz: "",
        sex: "",
        birthday: "",
        address: "", //详细地址
        buy: null,
        brand: "", //车牌号
        date: "", //买保险日期
        date1: "", // 购车日期
        date_sc: "", //审车日期
        date_jsz: "", //初次驾驶证
        Moto: [],
        OtherMoto: "",
        MotoType: "",
        Money: "",
        desc: "",
        addtowns: "",
        img: "",
        img_xszfm: "",
        img_qs: "",
        img_new: "",
        img_jsz: "",
        // towns: "",
        code: null,
        code_sc: null, //审车排序用
        bx_true: false,
        sc_true: false,
      },
      towns: "",
      formLabelWidth: "120px",
      rules: {
        username: [
          { required: true, message: "请输入用户姓名", trigger: "blur" },
        ],
        call: [
          { required: true, message: "联系电话不能为空", trigger: "blur" },
          { type: "number", message: "联系电话必须为数字" },
        ],
      },
    };
  },
  created() {},
  methods: {
    changeStatus(callback) {
      this.ruleForm.buy = callback;
      callback == true ? (this.show = "") : (this.show = "none");
    },
    handleRemove(file, fileList) {},
    img(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    imgChange(file, fileList) {
      this.ruleForm.img = file.raw;
    },
    // 车辆合格证
    img_qs(file) {
      this.img_qs = file.url;
      this.dialogVisible = true;
    },
    img_qs_change(file, fileList) {
      this.ruleForm.img_qs = file.raw;
    },
    // 新车合照
    img_new(file) {
      this.img_new = file.url;
      this.dialogVisible = true;
    },
    img_new_change(file, fileList) {
      this.ruleForm.img_new = file.raw;
    },
    // 驾驶证
    img_jsz(file) {
      this.img_jsz = file.url;
      this.dialogVisible = true;
    },
    img_jsz_change(file, fileList) {
      this.ruleForm.img_jsz = file.raw;
    },
    img_xszfm(file) {
      this.img_xszfm = file.url;
      this.dialogVisible = true;
    },
    img_xszfm_change(file, fileList) {
      this.ruleForm.img_xszfm = file.raw;
    },
    // 行驶证反面
    // 数据上传
    submitForm(ruleForm) {
      for (let key in this.ruleForm.Moto) {
        if (this.ruleForm.Moto[key] == "其他") {
          let index = this.ruleForm.Moto.indexOf("其他");
          this.ruleForm.Moto.splice(index, 1, this.ruleForm.OtherMoto);
        }
      }
      this.$refs["ruleForm"].validate((valid) => {
        if (valid) {
          this.ruleForm.date =
            this.ruleForm.date == null ? "" : this.ruleForm.date;
          this.ruleForm.date_sc =
            this.ruleForm.date_sc == null ? "" : this.ruleForm.date_sc;
          this.ruleForm.date1 =
            this.ruleForm.date1 == null ? "" : this.ruleForm.date1;

          if (this.ruleForm.date != null && this.ruleForm.date != "") {
            let date_ = this.ruleForm.date
              .toLocaleDateString()
              .slice(5, 7)
              .replace("/", "");
            this.ruleForm.code = date_;
            this.ruleForm.date = this.ruleForm.date.toLocaleDateString();
          }
          if (this.ruleForm.date_sc != null && this.ruleForm.date_sc != "") {
            let date_ = this.ruleForm.date_sc
              .toLocaleDateString()
              .slice(5, 7)
              .replace("/", "");
            this.ruleForm.code_sc = date_;
            this.ruleForm.date_sc = this.ruleForm.date_sc.toLocaleDateString();

          }
          // 购车日期
          if (this.ruleForm.date1 != null && this.ruleForm.date1 != "") {
            this.ruleForm.date1 = this.ruleForm.date1.toLocaleDateString();
          }
          // 初次驾驶证
          if (this.ruleForm.date_jsz != null && this.ruleForm.date_jsz != "") {
            this.ruleForm.date_jsz = this.ruleForm.date_jsz.toLocaleDateString();
          }
          let formData = new FormData();
          for (let key in this.ruleForm) {
            formData.append(key, this.ruleForm[key]);
          }
          // 添加按钮 提交到数据库

          this.$axios({
            method: "POST",
            url: "/api/adduser",
            data: formData,
          }).then((res) => {
            if (res.data.status === 1) {
              this.ruleForm.date1 = "";
              this.ruleForm.date = "";
              this.ruleForm.date_sc = "";
              this.ruleForm.date_jsz = "";
              this.$refs.upload.clearFiles();
              this.$refs.upload_qs.clearFiles();
              this.$refs.upload_new.clearFiles();
              this.$refs.upload_jsz.clearFiles();
              this.$refs.upload_img_xszfm.clearFiles();
              this.$refs[ruleForm].resetFields();
              this.show = "none";
              this.show_fm = "none";
              this.$message({
                message: "添加成功！",
                type: "success",
              });
            }
          });
        } else {
          return false;
        }
      });
    },

    resetForm(formName) {
      this.ruleForm.date1 = "";
      this.ruleForm.date = "";
      this.ruleForm.date_sc = "";
      this.ruleForm.date_jsz = "";
      this.$refs.upload.clearFiles();
      this.$refs.upload_qs.clearFiles();
      this.$refs.upload_new.clearFiles();
      this.$refs.upload_jsz.clearFiles();
      this.$refs.upload_img_xszfm.clearFiles();
      this.$refs[formName].resetFields();
      this.show = "none";
      this.show_fm = "none";
    },
    // 添加按钮
    add() {
      this.dialogFormVisible = true;
      this.ruleForm.addtowns = "";
    },
    // 判断男女
    sex() {
      if (this.ruleForm.sfz.length == 18) {
        let temp = this.ruleForm.sfz;
        this.ruleForm.sex = temp.charAt(16) % 2 == 0 ? "女" : "男";
        // 处理生日
        this.ruleForm.birthday =
          temp.substring(6, 10) +
          "-" +
          temp.substring(10, 12) +
          "-" +
          temp.substring(12, 14);
      }
    },
    // 其他摩托
    otherMoto() {
      this.ShowMoto = this.ShowMoto ? false : true;
    },
  },
  components: {},
};
</script>
<style scoped>
</style>